.footerComponent{
    padding-top: 20px;
    background-color: black;
    color: white;
}
.lists{
    list-style: none;
}
.listColor{
    color: grey;
}
.listColor:hover{
    cursor: pointer;
    color: white;
}
.icons{
    color: grey;
    padding: 5px;
}
.icons:hover{
    cursor: pointer;
    color: white;
}
.borderLR{
    border-right: grey solid 1px;
    border-left: grey solid 1px;
}
.twoLists{
    padding-right: 40px;
    list-style: none;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.secondSection{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.secondSectionLinks{
    color: grey;
    padding: 0 10px;
}
.secondSectionLinks:hover{
    color: white;
}
.citeSection{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 25px 15px
}
.cite{
    font-size: 12px;
    color: grey;
}


